import is from "./is";

/**
* 
* @param {Array} list 
* @param {String} property  children 
* @param {Boolean} keep  false 
* @param {Function} predicate 
*/
export default function flatten(list, property = "children", keep = false, predicate) {
    if (is.boolean(property)) {
        predicate = key;
        keep = property;
        property = "children";
    }

    let array = [];

    list.forEach(item => {
        let children = item[property];

        if (children) {
            if (keep) {
                array.push(item);
            }

            if (is.function(predicate) && !predicate(item)) {
                return;
            }

            array.push.apply(array, flatten(children, property, keep, predicate));
        }
        else {
            array.push(item);
        }
    });

    return array;
};


